html, body {
    overflow-x: hidden;
}
html {font-size: 100%;} /*16px*/
body {
  background: white;
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 400;
  line-height: 1.75;
  color: #000000;
}

p {margin-bottom: 1rem;}
p:first-of-type:not(.card-text){
    margin: 0;
}
h1, h2, h3, h4, h5 {
  margin: 3rem 0 2.38rem;
  font-family: 'Playfair Display', serif;
  font-weight: 400;
  line-height: 1.3;
    text-align: center;
}
h1 {
    margin-top: 0;
    font-size: 2rem;
}
h2 {font-size: 1.4rem;}
h3 {font-size: 1.2rem;}
h4 {font-size: 1.1rem;}
h5 {font-size: 1rem;}

small, .text_small {
    font-size: 0.8rem;
}
@media screen and (min-width: 576px) {
    h1 {
        margin-top: 0;
        font-size: 2.552rem;
    }

    h2 {font-size: 1.625rem;}
    h3 {font-size: 1.375rem;}
    h4 {font-size: 1.125rem;}
    h5 {font-size: 1rem;}

    small, .text_small {
        font-size: 0.8rem;
    }
}
@media screen and (min-width: 768px) {
    h1 {
      margin-top: 0;
      font-size: 3.052rem;
    }
    h2 {font-size: 2.441rem;}
    h3 {font-size: 1.953rem;}
    h4 {font-size: 1.563rem;}
    h5 {font-size: 1.25rem;}

    small, .text_small {font-size: 0.8rem;}
}

.btn {
    background: none;
    border: 0.175em solid whitesmoke;
    padding: 0.5em;
    color: whitesmoke;
    text-transform: uppercase;
    letter-spacing: 0.25em;
    border-radius: 0.5em;
    cursor: pointer;
    font-size: 0.8rem;
}
@media screen and (min-width: 1200px) {
    h1 {
      margin-top: 0;
      font-size: 3.552rem;
    }

    h2 {font-size: 3.441rem;}

    h3 {font-size: 1.953rem;}

    h4 {font-size: 2.063rem;}

    h5 {font-size: 1.75rem;}

    small, .text_small {font-size: 1.3rem;}

    .btn {
        font-size: 1rem;
    }
}

.btn:focus {
    outline: none;
}

.btn-big {
    padding: 1.4em 2.3em;
}

.btn-loading {
    border-color: #d5be51;
    color: #d5be51;
    background-image: linear-gradient(45deg, transparent 50%, #d5be51 50%);
    background-position: 25%;
    background-size: 400%;
    transition: background 500ms ease-in-out, color 500ms ease-in-out;
}

.btn-loading:hover {
    color: whitesmoke;
    background-position: 100%;

}

.bg-beige {
    background-color: #f3efde;
}

@media screen and (min-width: 992px) {
    .w-lg-50 {
        width: 50% !important;
    }
}

.navigation {
    margin: 0 0 100px;
}

nav.navbar {
    height: 100px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.nav-link {
    font-family: 'Playfair Display', serif;
    font-weight: bold;
}

.logo {
    height: 70px;
}

.flex-grow-2 {
    flex-grow: 2;
}

.Home, .AboutMe, .Projects, .Contact {
    margin: 1.5em 0;
}

.Contact {
    margin-bottom: 0;
}

.aboutme-header {
    height: 15vh;
}

@media screen and (min-width: 720px) {
    .Home, .AboutMe, .Projects, .Contact {
        margin: 3em 0;
    }

    .aboutme-header {
        height: 25vh;
    }
}

div.Home {
    background: url("./images/jukan-tateisi-bJhT_8nbUA0-unsplash.jpg");
    background-size: cover;
    height: calc(100vh - 100px);
}

@keyframes pop-in {
    0% {
        opacity: 0;
        transform: scale(0.5);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

