.shell * {
    font-family: 'monospace', sans-serif;
    font-weight: bolder;
}
.shell h1{
    margin: 0;
    text-align: left;
}
span.cl {
    color: white;
}

span.green {
    color: lawngreen;
}

span.blue {
    color: mediumslateblue;
}

a.typewrite {
    color: white;
    text-decoration: none;
    letter-spacing: unset !important;
}

a.typewrite:hover {
    cursor: default;
    text-decoration: none;
}
.aboutme{
    padding: 0;
}
.me-image{
    position: relative;
    overflow: hidden;
    flex: 0 0 45%;
    border-top-left-radius: 5em;
    border-bottom-right-radius: 0;
}
.me-image img {
    max-width: 100%;
	transform: none;
    position: relative;
    top: -5vh;
}

div.about {
    padding: 2rem;
    border-radius: 5em;
    border-bottom-left-radius: 0;
    background-color: #f3efde;
    margin: -15vh auto 0;
    z-index: 1;
}
div.about > p {
    text-align: justify;
    word-wrap: break-word;
    hyphens: auto;
}

div.about > p > span {
    font-weight: bold;
}
@media screen and (min-width: 768px){
    .me-image img {
        top: -10vh;
    }
    div.about {
        margin: -20vh auto 0;
    }
}
@media screen and (min-width: 992px){
    .me-image{

    border-bottom-right-radius: 5em;
    }
    .me-image img {
        top: 0;
        transform: scale(1.1, 1.1);
    }
    div.about {
        padding: 0 0 0 2rem;
        border-radius: 1em;
        margin: 0;
    }
}
@media screen and (max-width: 991.98px) {
    .img-container2 {
        background-color: transparent;
    }
}
.skill-jumbotron{
    background-color: transparent;
    padding-top: 0;
}.skill-container{
    padding: 0;
}

.skills {
    height: 1.5em;
    flex-grow: 1;
    animation-timing-function: ease-in-out;
    position: relative;
}
@media screen and (min-width: 314px) {

    .skills{
        height: 2em;
    }
}
@media screen and (min-width: 576px) {
    .skills{
        height: 3em;
    }
}
@media screen and (min-width: 720px) {
    .skills {
        height: 5em;
    }
}

.skills.left {
    animation: move-in-left 8s infinite;
    animation-timing-function: ease-in-out;
}

.skills.right {
    animation: move-in-right 8s infinite;
    animation-timing-function: ease-out;
}

@keyframes move-in-left {
    0% {
        transform: rotate(0deg);
        left: 101vw;
    }
    25% {
        transform: rotate(270deg);
        left: 101vw;
    }
    50% {
        transform: rotate(0deg);
        left: 0px;
    }
    55% {
        transform: rotate(0deg);
        left: 0px;
    }
    70% {
        transform: rotate(0deg);
        left: 0px;
    }
    70% {
        transform: rotate(-30deg);
        left: 0px;
    }
    100% {
        transform: rotate(0deg);
        left: 101vw;
    }
}

@keyframes move-in-right {
    0% {
        transform: rotate(0deg);
        right: 101vw;
    }
    25% {
        transform: rotate(-360deg);
        right: 101vw;
    }
    50% {
        transform: rotate(0deg);
        right: 0px;
    }
    55% {
        transform: rotate(0deg);
        right: 0px;
    }
    70% {
        transform: rotate(0deg);
        right: 0px;
    }
    90% {
        transform: rotate(20deg);
        right: 0px;
    }
    100% {
        transform: rotate(0deg);
        right: 101vw;
    }
}

/*projects start*/
.project-group {
    background-color: #f3efde;
    height: 150px;
    text-align: center;
}
.project-group h2{
    margin-top: 0;
}
.card-group {
    margin: -180px 0 100px;
    display: none;
}

.carousel {
    margin: -81px 0 50px;
}
@media screen and (min-width: 576px) {
    .project-group {
        height: 300px;
    }
    .carousel {
        margin: -195px 0 50px;
    }
}
.carousel-item-next, .carousel-item-prev, .carousel-item.active {
    display: flex;
}

.carousel-control-prev-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23d5be51' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5L4.25 4l2.5-2.5L5.25 0z'/%3e%3c/svg%3e") !important;
}

.carousel-control-next-icon {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23d5be51' width='8' height='8' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5L3.75 4l-2.5 2.5L2.75 8l4-4-4-4z'/%3e%3c/svg%3e") !important;
}

.carousel-indicators {
    bottom: -2.5rem;
}

.carousel-indicators li {
    background-color: #d5be51;
    height: 5px;
}

@media screen and (min-width: 768px) {
    .card-group.visible-md {
        display: flex;
    }

    .carousel.hidden-md {
        display: none;
    }
}

.card {
    flex: unset !important;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, .125) !important;
}

.card:hover .card-overlay.bottom {
    bottom: 0;
    padding: 1rem;
}

.card-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    overflow: hidden;
    text-align: center;
    /* fix text transition issue for .left and .right but need to overwrite left and right properties in .right */
    width: 100%;
    transition: bottom 0.3s;
    display: flex;
    align-items: center;
}
.card-body {
    padding: 0;
}
@media screen and (min-width: 448px) {
    .card:hover .card-overlay.bottom {
        padding: 1.25rem;
    }
}
.card-overlay.bottom {
    bottom: 100%;
}

.carousel-inner {
    width: 80%;
    left: 10%;
}


@media screen and (min-width: 576px) {
    .carousel-inner {
        width: 75%;
        left: 12.5%;
    }
}

@media screen and (min-width: 768px) {
    .card {
        width: 45%;
        margin: 0 0 1rem !important;
    }
}

@media screen and (min-width: 1140px) {
    .card {
        width: 32%;
    }
}

.btn-group-vertical {
    height: 6em;
}

.three-btns {
    height: 10em;
}

