/*contact start*/
.highlight {
    display: inline-block;
    color: #d5be51;
    transition: color 250ms;
    text-decoration: none;
    text-shadow: 0px 1px 0px rgba(255, 255, 255, 1);
    position: relative;
    z-index: 0;
}

.highlight:after {
    position: absolute;
    z-index: -1;
    bottom: -2px;
    left: 50%;
    transform: translateX(-50%);
    content: '';
    width: 100%;
    height: 3px;
    background-color: #d5be51;
    transition: all 250ms;
}

.highlight:hover {
    color: white;
    /*text-shadow: 0px 1px 0px rgba(0, 0, 0, 1);*/
}

.highlight:hover:after {
    height: 105%;
    width: 105%;
}

.form-row .form-group {
    width: 48%;
}

/*contact end*/